import Vue from "vue";
import VueRouter from "vue-router";

import MainMap from "../views/MainMap.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "map",
    component: MainMap,
  },
  {
    path: "*",
    name: "notfound",
    component: () =>
      import(/* webpackChunkName: "notfound" */ "../views/NotFound.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
